import vue from 'vue';
import vueRouter from 'vue-router'

const Index = () =>
  import('@/views/index/Index')
const Detail = () =>
  import('@/views/detail/Detail')
const price = () =>
  import('@/views/price/price')
const type_more = () =>
  import('@/views/type_more/type_more')
const car_img = () =>
  import('@/views/car_img/carImg')
const color = () =>
  import('@/views/car_img/color')
const carType = () =>
  import('@/views/car_img/carType')
const login = () =>
  import('@/views/login/login')

vue.use(vueRouter)
const router = new vueRouter({
  routes: [{
    path: '/index',
    name: 'index',
    component: Index
  }, {
    path: '/detail',
    component: Detail
  }, {
    path: '/price',
    component: price
  }, {
    path: '/type_more',
    component: type_more
  }, {
    path: '/carImg',
    component: car_img,
  }, {
    path: '/color',
    component: color,
  }, {
    path: '/carType',
    component: carType,
  }, {
    path: '/login',
    component: login,
  }, {
    path: '*',
    redirect: '/index'
  }]
})
router.beforeEach((to, from, next) => {
  var userInfo = window.sessionStorage.getItem('username'); //
  var userpassword = window.sessionStorage.getItem('password');
  if (userInfo && userpassword) { //
    next();
  } else {
    if (to.path == '/login') { //next()
      next();
    } else { //
      next('/login');
    }

  }
  next();
})
export default router



